.popup__container_type_info {
  width: 430px;
  height: 330px;
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
  justify-content: center;
}

@media (max-width: 460px) {
  .popup__container_type_info {
    width: 282px;
    margin-bottom: 45px;
  }
}
